@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body{margin:0 auto;max-width:1000px;font-family:'Lato', sans-serif;font-size:18px;color:#333;background:#FAFAFA;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}nav{font-family:'Montserrat', sans-serif}.main-wrapper{position:relative;min-height:100vh}main{padding:0 30px 120px;line-height:1.6}h1,h2,h3,h4{font-weight:bold;font-family:'Montserrat', sans-serif}h2{font-size:26px}a{text-decoration:none;transition:.3s all;color:#2D84FF}a:hover{color:#0067F9}p{font-size:20px}img{width:100%}.block{display:block;height:5px;width:100px}.button{background-color:#2D84FF;color:#fff;padding:15px 30px;border-radius:4px;text-decoration:none}.button:hover{background:#0067F9;color:#fff;transform:translateY(-3px);box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2)}.animate{animation-duration:.7s;animation-fill-mode:both}@keyframes fadeIn{from{opacity:0;transform:translate3d(0, 4%, 0)}to{opacity:1;transform:none}}.fadeIn{animation-name:fadeIn}@media only screen and (max-width: 700px){h2{font-size:24px}p{font-size:18px}footer ul{justify-content:space-evenly}}

.work{margin:45px 0 60px;display:flex;flex-wrap:wrap}.work-card{flex:0 1 50%;margin-bottom:30px;padding:10px;box-sizing:border-box}.work-card img{border-top-left-radius:8px;border-top-right-radius:8px;transition:.3s ease}.work-card .work-description{margin-top:-8px;padding:30px;border-bottom-left-radius:8px;border-bottom-right-radius:8px;box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2);transition:.3s ease}.work-card h3{margin:0;color:#fff}.work-card span{margin-bottom:10px;display:block;font-size:16px;color:#fff}.work-card small{font-size:14px;color:#fff}.work-card:nth-child(even){margin-top:-45px}.work-card:hover .work-description,.work-card:hover img{transition:.3s ease;transform:translateY(-5px)}.addon{background:#1E52A1}.edco{background:#F06D40}.proline{background:#C91417}.sn{background:#21355A}@media only screen and (max-width: 600px){.work{margin-top:30px}.work-card{flex:auto;margin-bottom:0}.work-card:nth-child(even){margin-top:0}}

.about{margin-top:30px;max-width:700px}.about .about-header{display:flex;align-items:center}.about .about-header h2{padding-right:15px}.quote{margin:45px 0;padding:5px 15px 15px;border-left:2px solid #FFA82D;background:#fafafa}

nav{display:flex;justify-content:space-between;align-items:center;border-bottom:2px solid #fff}nav div a{color:#333;font-size:20px;font-weight:500;text-decoration:none}nav div a:hover{color:#2D84FF}nav ul{display:flex}nav ul li{padding:0 15px;list-style:none}nav ul li a{color:#333;text-decoration:none;transition:all .2s}nav ul li a:hover{color:#2D84FF;cursor:pointer}

.posts .post{margin:15px 0;padding:20px;display:flex;justify-content:space-between;align-items:center;background:#fff;border-radius:8px;text-decoration:none;transition:.3s;border:1px solid #eee}.posts .post .date{display:block;font-size:16px}.posts .post:hover{transform:translateY(-3px);color:#2D84FF}

.post{margin-bottom:60px}.post .header-intro h2{margin-top:45px}.post .header-intro h4{margin-top:0}.post h2{margin-top:60px;margin-bottom:5px}.post p{margin-top:0;margin-bottom:30px}.post small{margin-bottom:5px;font-weight:bold;display:block;text-align:center}.post .sub-intro{margin-bottom:30px;display:flex}.post .sub-intro div{margin-right:60px}.post .sub-intro div h3{font-size:16px;margin-bottom:0;border-bottom:1px solid #eee}.post .sub-intro div p{margin:7px 0;line-height:1;font-size:14px}@media only screen and (max-width: 700px){.sub-intro{flex-direction:column}}

.home{padding-top:45px}.home .header{display:flex;align-items:center}.home .header h1{font-size:30px;line-height:1.2}.home p{max-width:580px;line-height:1.6}@media only screen and (max-width: 700px){.home .header{flex-direction:column;align-items:flex-start}}

footer{padding:30px 0;position:absolute;bottom:0;width:100%;font-family:'Montserrat', sans-serif}footer ul{display:flex;padding-left:25px}footer ul li{margin-right:30px;list-style:none}footer ul li a:hover{color:#2D84FF}footer a{color:#333;text-decoration:none;transition:all .2s}

